.wrapper__modal--position {
  width: 100%;

  min-height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}
.wrapper__modal {
  width: 500px;
  height: 200px;
  border-radius: 20px;
  background-color: white;
  z-index: 100;
}
.wrapper__modal_block {
  display: flex;
  justify-content: start;

  color: #ffffff;
  font-size: 25px;
  padding: 10px 20px;
  background-color: #272e4e;
  border-radius: 20px 20px 0 0;
  height: 50px;
  z-index: 100;
}
.wrapper__modal_p {
  display: flex;
  justify-content: start;
  font-size: 25px;
  padding: 10px 20px;
}
.wrapper__modal_button {
  margin: 20px;

  width: 150px;
  height: 40px;
  border: 0;
  border-radius: 10px;
  background-color: #e87d7d;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
  font-size: 25px;
}
.wrapper__modal_button:hover {
  background-color: #d16b6b;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.7);
}

