.wrapper__form {
  padding-bottom: 20px;
}
.wrapper__form_h1 {
  padding-top: 20px;

  color: #ffffff;
  font-size: 50px;
}
.wrapper__form_input {
  margin-top: 20px;

  width: 300px;
  height: 30px;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
  font-size: 20px;
  padding: 2px 5px;
}
.wrapper__form_button {
  margin: 20px;

  width: 150px;
  height: 40px;
  border: 0;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
  font-size: 25px;
}
.wrapper__form_button:hover {
  background-color: #919191;
}

@media only screen and (max-width: 1200px) {
}
@media only screen and (max-width: 992px) {
}
@media only screen and (max-width: 768px) {
  .wrapper__form_h1 {
    padding-top: 30px;
  }
}
@media only screen and (max-width: 480px) {
    .wrapper__form_input {
        width:250px;
    }
    .wrapper__form_h1 {
        font-size:40px;
    }
    .wrapper__form_button {
        width:120px;
        height: 35px;
        font-size:18px;
    }
}
@media only screen and (max-width: 320px) {
}
