.wrapper__card{
    width: 80%;
    height: 70px;
    margin: 30px auto;
    background-color:#ffffff; 

    display: flex;
    justify-content: space-between;
    align-items: center; 
}
.wrapper__card_p{
    font-size: 25px;
    padding-left: 110px;
    margin: 0 auto;
}
.img{
    width:50px;
    height: 50px;
    padding-right: 10px;
}
.wrapper__card_img-position{
    display: flex;
    align-items: center;
}
.wrapper__card_input{
    /* margin-left:120px; */
    /* margin: 0 auto; */
    width: 300px;
    height: 30px;
    border: 1px solid;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
    font-size: 25px;
    padding: 2px 5px;
}
.wrapper__card_edit{
    margin: 0 auto;
    /* padding-left: 120px; */
    margin-left:10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; 
}
.wrapper__card_container_img{
    display:flex;
}

@media only screen and (max-width: 1200px){
  
}
@media only screen and (max-width: 992px){
 
}
@media only screen and (max-width: 768px){
    .wrapper__card{
        width: 90%;
    }
    .img{
        width:30px;
        height: 30px;
        padding-right: 10px;
    }
    .wrapper__card_p{
        font-size: 23px;
        padding-left: 0;
        padding-right: 10%;/* 110px */
        margin-left: 10px;
    }
    .wrapper__card_edit{
        margin-left:10px;
        justify-content: space-between;
    }
    .wrapper__card_input{
        font-size: 20px;
    }
}
@media only screen and (max-width: 480px){
    .wrapper__card_p{
        font-size: 18px;
    }
    .wrapper__card_input{
        width:180px;
    }
}
