.App__container{
  width: 100%;
  min-height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
  width: 50%;
  height: auto;
  min-height: 680px;
  padding-bottom: 30px;
  background-color: #272e4e;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
  margin: 10px auto;
  border-radius: 30px;
}
.p{
  margin-top: 50px;
  font-size: 40px;
  color: #ffffff;
}

@media only screen and (max-width: 1200px){
  .App{
    width: 70%;
  }
}
@media only screen and (max-width: 992px){
  .App{
    width: 80%;
  }
}
@media only screen and (max-width: 768px){
  .App{
    width: 100%;
    min-height: 100vh;
    margin: 0 auto;
    border-radius: 0;
  }
}
@media only screen and (max-width: 480px){
  .p{
    font-size: 30px;
  }
}
@media only screen and (max-width: 320px){

}